/**========== HEADER STYLING START ===========**/

.header {
  position: relative;
  width: 100%;
  height: 100vh;
}

.header-container {
  position: relative;
  display: grid;
  place-content: center;
  place-items: center;
  text-align: center;
  width: 100%;
  height: 100vh;
}

.header-bg {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  background: radial-gradient(
    circle at 0%,
    var(--clr-secondary) 0%,
    var(--clr-primary) 100%
  );
  mix-blend-mode: difference;
  z-index: -1;
}

.header-title {
  color: #fff;
  font-size: 35px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 35px;
  margin-bottom: 10px;
}

.header-title span {
  font-weight: 900;
}

.header-text {
  color: var(--txt-clr);
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;
  word-wrap: break-word;
  width: 90%;
  margin-bottom: 30px;
}

.header-btn {
  color: #fff;
  background-color: var(--clr-light);
  border: none;
  border-radius: 10px;
  padding: 10px 25px;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
}

/*!=========== HEADER STYLING END ============!*/
