@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700;800;900&display=swap");

:root {
  --clr-bg: #151515;
  --clr-body: #182434;
  --clr-primary: #073068;
  --clr-secondary: #175edc;
  --clr-light: #02a5e0;
  --clr-dark: #091133;

  --txt-clr: #ccc;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Nunito", Arial, Helvetica, sans-serif;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  background-image: url("images/tg-pattern-wn-black.jpg");
  background-size: cover;
  /* font-family: "Nunito", Arial, Helvetica, sans-serif; */

  color: #fff;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

ul,
ol {
  list-style: none;
}

a {
  color: #fff;
  text-decoration: none;
}

.padding {
  padding-left: 25px;
  padding-right: 25px;
}

.spacer {
  padding-top: 30px;
  padding-bottom: 30px;
}

