.section {
  width: 100%;
  height: 100vh;
}

.section.home {
  background-color: darkslateblue;
}
.section.services {
  background-color: darkslategray;
}
.section.about {
  background-color: darkseagreen;
}
.section.work {
  background-color: darkorange;
}
.section.contact {
  background-color: darkmagenta;
}

