/**========== NAVBAR STYLING START ===========**/

.navbar {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1440px;
  width: 100%;
  height: 8vh;
  top: 0;
  left: 0;
  right: 0;
  margin: 20px auto 0 auto;
  z-index: 9999;
}

.nav-menu {
  position: fixed;
  display: flex;
  flex-direction: column;
  place-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: -100%;
  background: linear-gradient(var(--clr-secondary) 0%, var(--clr-primary) 100%);
  transition: 0.3s ease-out;
}

.navbar.active .nav-menu,
.navbar.active .social-menu {
  left: 0;
}

.nav-item {
  margin: 2rem 0;
}

.nav-link {
  font-size: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
}

.nav-logo {
  position: relative;
  display: block;
  width: 45px;
  align-items: center;
  z-index: 1;
}

.social-menu {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: fit-content;
  margin: 4rem 0;
  left: -100%;
  bottom: 0;
  transition: 0.3s ease-out;
}

.social-icon {
  font-size: 2.2rem;
  padding: 0 0.5em;
  color: #fff;
}

.hamburger {
  position: relative;
  display: block;
  cursor: pointer;
  z-index: 1;
}

.navbar.active .hamburger span:nth-child(1) {
  transform: translate(0px, 0px) rotate(45deg);
  background-color: var(--clr-primary);
}

.navbar.active .hamburger span:nth-child(2) {
  opacity: 0;
  transform: translateX(15px);
}

.navbar.active .hamburger span:nth-child(3) {
  transform: translate(-3px, 1px) rotate(-45deg);
  background-color: var(--clr-primary);
}

.bar {
  display: block;
  width: 30px;
  height: 5px;
  margin: 5px auto;
  border-radius: 50px;
  background-color: #fff;
  transform-origin: 0 0;
  transition: 0.4s ease;
}

/*!=========== NAVBAR STYLING END ============!*/

